.plot {
  width: 50px;
  height: 50px;
  border: 1px solid #000;
  display: inline-block;
  text-align: center;
  line-height: 50px;
  font-size: 12px;
  cursor: pointer;
}

.house {
  background-color: green;
}

.restaurant {
  background-color: grey;
}

.gym {
  background-color: rgb(10, 17, 78);
}

.hospital {
  background-color: red;
}
